@use 'variables' as v;
@use 'base/mixins' as  m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
    // user-select: none;
    background-color: v.$fondo;
}

p {
    font-size: 1.6rem;
    @include m.tablet {
        font-size: 1.8rem;
    }
    @include m.desktop {
        font-size: 2rem;
    }
}

.contenedor {
    width: 90%;
    margin: 0 auto;
    @include m.telefono {
        max-width: 40rem;
    }
    @include m.telefonoXL {
        max-width: 50rem;
    }
    @include m.tablet {
        max-width: 60rem;
    }
    @include m.tabletXL {
        max-width: 70rem;
    }
    @include m.desktop {
        max-width: 100rem;
    }
    @include m.desktopXL {
        max-width: 120rem;
    }
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}