.header {
  background-color: #ffffff;
  padding: 2rem;
  margin: 0;
}
.header__grid {
  display: flex;
  gap: 2rem;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-around;
  }
}
.header__nav__pri, .header__nav__sec {
  display: none;
}
@media (min-width: 768px) {
  .header__nav__pri, .header__nav__sec {
    padding: 0.6rem 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    gap: 5rem;
  }
}
@media (min-width: 768px) {
  .header__nav__pri {
    border-right: 2px solid #daddd8;
  }
}
@media (min-width: 768px) {
  .header__nav__sec {
    border-left: 2px solid #daddd8;
  }
}
.header__link {
  text-transform: uppercase;
  color: #1c1c1c;
  font-size: 1.6rem;
  font-weight: 700;
  padding: 0.6rem;
  transition-property: border;
  transition-duration: 0.3s;
  border-top: 4px solid #ffffff;
  border-bottom: 4px solid #ffffff;
}
.header__link:hover {
  border-bottom-color: #daddd8;
}
.header__img__perfil {
  background-color: #daddd8;
  background-image: url("../img/foto_perfil.webp");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center -1rem;
  width: 10rem;
  height: 10rem;
  border: 4px solid #daddd8;
  border-radius: 100%;
  order: -1;
}
@media (min-width: 768px) {
  .header__img__perfil {
    order: 0;
  }
}

.bienvenida {
  background-color: #1c1c1c;
  margin-top: -7rem;
  padding: 4rem 2rem;
}
@media (min-width: 768px) {
  .bienvenida {
    margin: 0;
    height: auto;
    justify-content: space-between;
  }
}
.bienvenida__grid {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: space-evenly;
  height: 30rem;
}
.bienvenida__h1 {
  color: #FC7643;
  font-size: 3rem;
  width: 30rem;
  margin: 0 auto;
  padding-top: 1rem;
}
@media (min-width: 768px) {
  .bienvenida__h1 {
    padding-top: 0;
  }
}
.bienvenida__span {
  color: #ffffff;
  display: block;
  font-size: 1.2rem;
  margin-top: -10px;
  text-transform: capitalize;
  border-bottom: 2px solid #daddd8;
}
.bienvenida__descripcion {
  text-align: center;
}
.bienvenida__texto {
  color: #ffffff;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bienvenida__texto {
    width: 50%;
  }
}
.bienvenida__botones {
  display: flex;
  flex-direction: column-reverse;
  gap: 1rem;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bienvenida__botones {
    width: 50rem;
    flex-direction: row;
  }
}
.bienvenida__boton {
  background-color: #daddd8;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #1c1c1c;
  text-decoration: none;
