}
@media (min-width: 768px) {
  .bienvenida__boton {
    width: 30rem;
  }
}
.bienvenida__boton:first-child {
  background-color: #FC7643;
}
.bienvenida__boton:first-child:hover {
  cursor: pointer;
  background-color: #fb5111;
}
@media (min-width: 768px) {
  .bienvenida__boton {
    width: 20rem;
    font-size: 1.6rem;
  }
