.trabajos {
  background-image: url(../img/fondo-panel.avif);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 2rem 0 4rem 0;
  border-top: 4px solid #1c1c1c;
  border-bottom: 4px solid #1c1c1c;
  box-shadow: 0 0 3.2rem -0.8rem #2B2B2B;
}
.trabajos__h2-trabajos {
  color: #ffffff;
  margin: 0 auto 2rem auto;
  width: 20rem;
  font-size: 2.6rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
}
.trabajos__grid {
  width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .trabajos__grid {
    display: grid;
    grid-template-columns: 3fr 1fr;
    gap: 2rem;
  }
}
.trabajos__panel {
  border: 0.5rem solid #1c1c1c;
  background-color: rgba(28, 28, 28, 0.8);
  color: #ffffff;
  display: none;
  max-width: 100%;
  max-height: 100%;
}
@media (min-width: 768px) {
  .trabajos__panel {
    display: flex;
    flex-direction: column;
  }
}
.trabajos__panel-header {
  display: grid;
  grid-template-columns: 2fr 3fr;
  gap: 1rem;
  align-items: center;
  padding: 2rem;
}
.trabajos__informacion {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1rem;
}
.trabajos__header-titulo {
  display: flex;
