  color: #FC7643;
  padding-top: 1rem;
  margin: 0;
  font-size: 1.3rem;
  user-select: none;
}
.trabajos__fecha {
  display: block;
  font-size: 1rem;
  color: #8c9686;
  margin: 0;
  padding: 0;
}
.trabajos__performance, .trabajos__PAB {
  display: flex;
  flex-direction: row;
}
.trabajos__performance {
  justify-content: space-around;
  gap: 2rem;
}
.trabajos__PAB {
  justify-content: space-between;
