}
.trabajos__SEObox {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: center;
}
.trabajos__porcentaje {
  font-size: 1rem;
  font-weight: 700;
  color: #ffffff;
  width: 3.4rem;
  height: 3.4rem;
  border-radius: 100%;
