  padding: auto auto;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
}
.trabajos__SEOtexto {
  font-size: 1.2rem;
}
.trabajos__parrafo {
  font-size: 1.6rem;
  text-align: justify;
  color: #8c9686;
  grid-column: 1/3;
}
.trabajos__btn-visitar {
  grid-column: 1/3;
  background-color: #1c1c1c;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  user-select: none;
  border: 2px solid #2B2B2B;
  width: 16rem;
  height: 3rem;
  font-size: 1rem;
  font-weight: 700;
  text-align: center;
  padding: 0;
  margin: 3rem auto 0 auto;
  text-transform: capitalize;
}
.trabajos__btn-visitar:hover {
  cursor: pointer;
  background-color: #030303;
}
@media (min-width: 768px) {
  .trabajos__btn-visitar {
    width: 30rem;
  }
}
@media (min-width: 768px) {
  .trabajos__btn-visitar {
    width: 20rem;
    font-size: 1.6rem;
  }
}
.trabajos__btn-visitar:hover {
  cursor: pointer;
}
.trabajos__recPri {
  border: 2px solid #1c1c1c;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.trabajos__recBox {
  background-color: #1c1c1c;
  color: #ffffff;
  padding: 1rem;
  height: 12rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  overflow: hidden;
  margin-bottom: -0.2rem;
  user-select: none;
}
.trabajos__recBox:active {
  background-color: rgba(255, 255, 255, 0.5);
  color: #1c1c1c;
}
.trabajos__recBox:hover {
  cursor: pointer;
}
.trabajos__logo {
  padding: 1rem;
  background-color: #ffffff;
  border-radius: 100%;
