  height: 6rem;
}
.trabajos__imagen-logo {
  width: 4rem;
  height: 4rem;
}
.trabajos__detalles {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.trabajos__h3 {
  font-size: 1.4rem;
  padding: 0.5rem 0;
  margin: 0;
}
.trabajos__verPun {
  color: #ffffff;
}

.slider {
  width: 30rem;
  margin: auto;
  overflow: hidden;
}
.slider__ul {
  display: flex;
  padding: 0;
  width: 400%;
  animation: cambio 20s infinite alternate;
  animation-timing-function: ease-in;
}
.slider__li {
  width: 100%;
  list-style: none;
}
.slider__imagen {
  width: 100%;
}

@keyframes cambio {
  0% {
    margin-left: 0;
  }
  20% {
    margin-left: 0;
  }
  25% {
    margin-left: -100%;
  }
  45% {
    margin-left: -100%;
  }
  50% {
    margin-left: -200%;
  }
  70% {
    margin-left: -200%;
  }
  75% {
    margin-left: -300%;
  }
  100% {
    margin-left: -300%;
  }
}
