@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.trabajos {
    background-image: url(../img/fondo-panel.avif);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 2rem 0 4rem 0;
    border-top: 4px solid v.$primario;
    border-bottom: 4px solid v.$primario;
    box-shadow: 0 0 3.2rem -0.8rem v.$negro;
    &__h2-trabajos {
        color: v.$letrasDos;
        margin: 0 auto 2rem auto;
        width: 20rem;
        font-size: 2.6rem;
        text-align: center;
        text-transform: uppercase;
        font-weight: 700;
    }
    &__grid {
        width: 90%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        @include m.tablet {
            display: grid;
            grid-template-columns: 3fr 1fr;
            gap: 2rem;
        }
    }
    &__panel {
        border: 0.5rem solid v.$primario;
        background-color: v.$primarioOpacity;
        color: v.$letrasDos;
        display: none;
        @include m.tablet {
            display: flex;
            flex-direction: column;
        }
        max-width: 100%;
        max-height: 100%;
    }
    &__panel-header {
        display: grid;
        grid-template-columns: 2fr 3fr;
        gap: 1rem;
        align-items: center;
        padding: 2rem;
    }
    &__informacion {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 1rem;
    }
    &__header-titulo {
        display: flex;
        flex-direction: row;
        gap: 1rem;
        align-items: flex-start;
        justify-content: space-between;
        padding: 0;
        margin: 0 0 2rem 0;
    }
    &__h3-trabajos {
        font-size: 2.2rem;
        margin: 0;
        padding: 0;
    }
    &__version {
        color: v.$letrasTitulo;
        padding-top: 1rem;
        margin: 0;
        font-size: 1.3rem;
        user-select: none;
    }
    &__fecha {
        display: block;
        font-size: 1.0rem;
        color: darken(v.$secundario, 30);
        margin: 0;
        padding: 0;
    }
    &__performance,
    &__PAB {
        display: flex;
        flex-direction: row;
    }
    &__performance {
        justify-content: space-around;
        gap: 2rem;
    }
    &__PAB {
        justify-content: space-between;
        gap: 1rem;
    }
    &__SEObox {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        align-items: center;
    }
    &__porcentaje {
        font-size: 1.0rem;
        font-weight: 700;
        color: v.$letrasDos;
        width: 3.4rem;
        height: 3.4rem;
        border-radius: 100%;
        border: 2px solid v.$verde;
        padding: auto auto;
        display: flex;
        align-items: center;
        justify-content: center;
        user-select: none;
    }
    &__SEOtexto {
        font-size: 1.2rem;
    }
    &__parrafo{
        font-size: 1.6rem;
        text-align: justify;
        color: darken(v.$secundario, 30);
        grid-column: 1 / 3;
    }
    &__btn-visitar {
        grid-column: 1 / 3 ;
        @include m.boton(v.$primario, v.$letrasDos);
        border: 2px solid v.$negro;
        width: 16rem;
        height: 3rem;
        font-size: 1.0rem;
        font-weight: 700;
        text-align: center;
        padding: 0;
        margin: 3rem auto 0 auto;
        text-transform: capitalize;
        @include m.tablet {
            width: 20rem;
            font-size: 1.6rem;
        }
        &:hover {
            cursor: pointer;
        }
    }
    &__recPri {
        border: 2px solid v.$primario;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    &__recBox {
        background-color: v.$primario;
        color: v.$letrasDos;
        padding: 1rem;
        height: 12rem;
        display: grid;
        grid-template-columns: 1fr 2fr;
        align-items: center;
        overflow: hidden;
        margin-bottom: -0.2rem;
        user-select: none;
        &:active {
            background-color: v.$fondoOpacity;
            color: v.$letras;
        }
        &:hover {
            cursor: pointer;
        }
    }
    &__logo {
        padding: 1rem;
        background-color: v.$fondo;
        border-radius: 100%;
        width: 6rem;
        height: 6rem;
    }
    &__imagen-logo {
        width: 4rem;
        height: 4rem;
    }
    &__detalles {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    &__h3 {
        font-size: 1.4rem;
        padding: .5rem 0;
        margin: 0;
    }
    &__verPun {
        color: v.$letrasDos;
    }
}

.slider {
    width: 30rem;
    margin: auto;
    overflow: hidden;
    &__ul {
        display: flex;
        padding: 0;
        width: 400%;

        animation: cambio 20s infinite alternate;
        animation-timing-function: ease-in;
    }
    &__li {
        width: 100%;
        list-style: none;
    }
    &__imagen {
        width: 100%;
    }
}
@keyframes cambio {
    0% {margin-left: 0;}
    20% {margin-left: 0;}
    
    25% {margin-left: -100%;}
    45% {margin-left: -100%;}
    
    50% {margin-left: -200%;}
    70% {margin-left: -200%;}

    75% {margin-left: -300%;}
    100% {margin-left: -300%;}
}