@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.bienvenida {
    background-color: v.$primario;
    margin-top: -7rem;
    padding: 4rem 2rem;
    @include m.tablet {
        margin: 0;
        height: auto;
        justify-content: space-between;
    }
    &__grid {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        justify-content: space-evenly;
        height: 30rem;
    }
    &__h1 {
        color: v.$letrasTitulo;
        font-size: 3rem;
        width: 30rem;
        margin: 0 auto;
        padding-top: 1rem;
        @include m.tablet {
            padding-top: 0;
        }
    }
    &__span {
        color: v.$letrasDos;
        display: block;
        font-size: 1.2rem;
        margin-top: -10px;
        text-transform: capitalize;
        border-bottom: 2px solid v.$secundario;
    }
    &__descripcion {
        text-align: center;
    }
    &__texto{
        color: v.$letrasDos;
        width: 90%;
        margin: 0 auto;
        @include m.tablet {
            width: 50%;
        }
    }
    &__botones {
        display: flex;
        flex-direction: column-reverse;
        gap: 1rem;
        width: 100%;
        @include m.tablet {
            width: 50rem;
            flex-direction: row;
        }
        margin: 0 auto;
    }
    &__boton {
        @include m.boton(v.$secundario, v.$letras);
        &:first-child {
            background-color: v.$letrasTitulo;
            &:hover {
                cursor: pointer;
                background-color: darken(v.$letrasTitulo, 10);
            }
        }
        width: 80%;
        height: 4rem;
        font-size: 1.3rem;
        text-align: center;
        padding: 1rem;
        @include m.tablet {
            width: 20rem;
            font-size: 1.6rem;
        }
        margin: 0 auto;
        text-transform: uppercase;
    }
}