@use 'variables' as v;
@use 'base/mixins' as  m;

h1, h2, h3 {
    font-family: v.$fuentePrincipal;
    margin: 0 0 v.$separacion 0;
    text-transform: uppercase;
    text-align: center;
    line-height: 1.5;
}

h1 {
    font-size: 3.5rem;
    margin: 0;
    @include m.tablet {
        font-size: 4rem;
    }
    @include m.desktop {
        font-size: 4.5rem;
    }
}

h2 {
    font-size: 2.6rem;
    margin: .2rem;
    color: v.$primario;
    line-height: 1;
    text-transform: capitalize;
    @include m.tablet {
        font-size: 3rem;
    }
    @include m.desktop {
        font-size: 3.5rem;
    }
    span {
        color: v.$secundario;
        font-size: 1.2rem;
        display: block;
    }

    // &::after {
    //     content: '';
    //     display: block;
    //     width: 3rem;
    //     height: 3rem;
    //     margin: 0 auto;
    //     background-image: url(../img/h2-white.png);
    //     background-size: 3rem;
    //     background-position: bottom;
    // }
}

h3 {
    font-size: 2.6rem;
    @include m.tablet {
        font-size: 2.8rem;
    }
    @include m.desktop {
        font-size: 3rem;
    }
}