@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.recientes {
    width: 90%;
    margin: 0 auto;
    &__grid {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        @include m.tablet {
            @include m.grid(3, 3rem);
        }
    }
    &__contenedor {
        border: 2px solid v.$primario;
        max-height: 30rem;
    }
    &__imagen {
        height: 14rem;
        width: 100%;
        margin: 0 auto;
    }
    &__informacion {
        background-color: v.$primario;
        padding: 6px 2rem;
    }
    &__h3 {
        color: v.$letrasDos;
        font-size: 2rem;
        line-height: 1;
        text-transform: capitalize;
        padding: 1rem 0 4px 0;
        margin: 0;
    }
    &__version,
    &__fecha {
        color: v.$letrasTitulo;
        font-size: 1.2rem;
        text-align: center;
        display: block;
    }
    &__texto {
        color: v.$secundario;
        margin: 0;
        padding: 1rem;
        text-align: center;
        font-size: 1.7rem;
    }
}