@use 'variables' as v; 
@use 'mixins' as m; 

.text-center {
    text-align: center;
}

.bg-primario {
    background-color: v.$primario;
}

.pad {
    padding: 3rem 0 2rem 0;
}

[class*="__header"] {
    margin: 4rem 0 4rem 0;
}

[class*="__titulo-pri"] {
    color: v.$blanco;
    text-transform: capitalize;
    line-height: 0.8;
    color: v.$blanco;
    font-weight: 400;
}
[class*="__span-tit"] {
    color: v.$negro;
    font-weight: 700;
}
[class*="__span-min"] {
    color: v.$primario;
    display: block;
    font-size: 2rem;
    font-weight: 700;
}

.non {
    display: none;
}

.espaciado {
    padding: 4.5rem;
}