@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.header {
    background-color: v.$fondo;
    padding: 2rem;
    margin: 0;
    &__grid {
        display: flex;
        gap: 2rem;
        align-items: center;
        justify-content: center;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-around;
        }
    }
    &__nav__pri,
    &__nav__sec {
        display: none;
        @include m.tablet {
            padding: 0.6rem 1rem;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            align-items: center;
            gap: 5rem;
        }
    }
    &__nav__pri {
        @include m.tablet { 
            border-right: 2px solid v.$secundario;
        }
    }
    &__nav__sec {
        @include m.tablet {
            border-left: 2px solid v.$secundario;
        }
    }
    &__link {
        text-transform: uppercase;
        color: v.$letras;
        font-size: 1.6rem;
        font-weight: 700;
        padding: 0.6rem;
        transition-property: border;
        transition-duration: .3s;
        border-top: 4px solid v.$fondo;
        border-bottom: 4px solid v.$fondo;
        &:hover {
            border-bottom-color: v.$secundario
        }
    }
    &__img {
        &__perfil {
            background-color: v.$secundario;
            background-image: url("../img/foto_perfil.webp");
            background-repeat: no-repeat;
            background-size: 100%;
            background-position: center -1rem;
            width: 10rem;
            height: 10rem;
            border: 4px solid v.$secundario;
            border-radius: 100%;
            order: -1;
            @include m.tablet {
                order: 0;
            }
        }
    }
}