@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.destacados {
    &__h2-destacados {
        color: v.$letras;
        margin: 0 auto 2rem auto;
        width: 20rem;
        font-size: 2.6rem;
        text-align: center;
        text-transform: uppercase;
        font-weight: 700;
    }
    &__grid {
        margin: 0 auto;
        width: 90%;
        display: flex;
        flex-direction: column;
        gap: 6rem;
        @include m.tablet {
            flex-direction: row;
        }
    }
    &__box {
        padding: 2rem;
        transition-property: box shadow;
        transition-duration: 0.4s;
        &:hover {
            box-shadow: 0 0 1.6rem -0.8rem v.$negro;
        }
    }
    &__h3 {
        padding: 0;
        padding-bottom: 1rem;
        margin: 0;
        margin-top: 2rem;
        font-size: 2.2rem;
        text-align: start;
    }
    &__texto {
        padding: 0;
        margin: 0;
        font-size: 1.6rem;
        text-align: justify;
        color: darken(v.$secundario, 30);
    }
}